#about {
  min-height: 100vh;
  padding-top: 80px;
}
#about p {
  padding: 30px;
}

#about .about-me {
  font-size: 60px;
  margin: -20px 0 -20px 0;
  transform: skew(15deg);
}

.skills-list {
  padding-top: 30px;
  margin: 0 50px 0 50px;
  display: grid;
  grid-gap: 10px;
  justify-items: start;
  grid-template-columns: 1fr;
  font-size: 30px;
  list-style-type: none;
}

.about-container {
  background: #303c48;
  border-radius: 5px;
  display: inline-block;
  transform: skew(-15deg);
  margin-left: 13vw;
}

.about-content {
  padding-top: 50px;
  margin: 0 20vw 0 20vw;
}
